<template>
	<div id="statcenter-errander-rank">
		<public-header title="跑腿排行榜"></public-header>
		<div class="content">
			<div class="wrap-search">
				<div class="tab-group flex-lr">
					<div class="tab-item" :class="{active: filter.items.type == 'today'}" @click="onToggleParams('type', 'today')">今日</div>
					<div class="tab-item" :class="{active: filter.items.type == 'yesterday'}" @click="onToggleParams('type', 'yesterday')">昨日</div>
					<div class="tab-item" :class="{active: filter.items.type == 'month'}" @click="onToggleParams('type', 'month')">本月</div>
					<div class="tab-item" :class="{active: filter.items.type == 'last_month'}" @click="onToggleParams('type', 'last_month')">上月</div>
				</div>
				<ul class="tab-other flex-lr padding-10-tb padding-15-lr bg-default">
					<li class="tab-other-item" :class="{active: filter.items.sort_type == 'total_success_order'}" @click="onToggleParams('sort_type', 'total_success_order')">单量</li>
					<li class="tab-other-item" :class="{active: filter.items.sort_type == 'percent_normal'}" @click="onToggleParams('sort_type', 'percent_normal')">准时率</li>
					<li class="tab-other-item" :class="{active: filter.items.sort_type == 'avg_delivery_success_time'}" @click="onToggleParams('sort_type', 'avg_delivery_success_time')">平均时长</li>
				</ul>
			</div>
			<div class="myself">
				<div class="list-item flex">
					<div class="rank-num">
						{{mine.ranking}}
					</div>
					<div class="rider flex-lr">
						<div class="info">
							<div class="avatar flex">
								<img :src="mine.avatar">
							</div>
							<div class="name ellipsis">{{mine.title}}</div>
						</div>
						<div class="scores">{{mine[filter.items.sort_type]}}{{dataUnit[filter.items.sort_type]}}</div>
					</div>
				</div>
			</div>
			<ul class="rank-list padding-15-lr">
				<li class="list-item flex-lr van-hairline--bottom" v-for="(item, index) in rank" :key="index">
					<div class="rank-num" v-if="index <= 2">
						<img :src="`static/img/medal-${index + 1}.png`" alt="">
					</div>
					<div class="rank-num" v-else>
						{{index+1}}
					</div>
					<div class="rider flex-lr">
						<div class="info">
							<div class="avatar flex">
								<img :src="item.avatar" />
							</div>
							<div class="name ellipsis">{{item.title}}</div>
						</div>
						<div class="scores">{{item[filter.items.sort_type]}}{{dataUnit[filter.items.sort_type]}}</div>
					</div>
				</li>
			</ul>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				mine: {},
				rank: [],
				filter: {
					items: {
						type: 'today',
						sort_type: 'total_success_order'
					}
				},
				dataUnit: {
					total_success_order: '单',
					percent_normal: '%',
					avg_delivery_success_time: '分钟'
				},
				showPreLoading: true
			}
		},
		methods: {
			onToggleParams(key, value) {
				var that = this;
				if(that.filter.items[key] == value) {
					return;
				} else {
					that.filter.items[key] = value;
					that.onLoad();
				}
			},
			onLoad() {
				var that = this;
				getData({
					url: 'delivery/statcenter/stat/rank_errander',
					vue: that,
					data: {
						filter: JSON.stringify(that.filter.items)
					},
					success: (result) => {
						that.mine = that.util.extend(that.mine, result.mine);
						if(result.rank && result.rank.length > 0) {
							that.rank = [...result.rank];
						}
					}
				})
			}
		},
		mounted() {
			this.onLoad();
		},
		components: {
			publicHeader,
		}
	}
</script>

<style>
	#statcenter-errander-rank .content{
		padding-top: 95px;
		background-color: #ffffff;
	}
	#statcenter-errander-rank .tab-other .tab-other-item{
		height: 30px;
		width: 50%;
		line-height: 30px;
		text-align: center;
		border-right: 1px solid #0EC3B3;
		border-top: 1px solid #0EC3B3;
		border-bottom: 1px solid #0EC3B3;
		box-sizing: border-box;
		font-size: 14px;
	}
	#statcenter-errander-rank .tab-other .tab-other-item:first-child{
		border-left: 1px solid #0EC3B3;
	}
	#statcenter-errander-rank .tab-other .tab-other-item.active{
		background-color: #0EC3B3;
		color: #fff;
	}
	#statcenter-errander-rank .rank-list{
		padding: 0 15px;
	}
	#statcenter-errander-rank .list-item{
		padding: 15px;
		font-size: 14px;
	}
	#statcenter-errander-rank .list-item .rank-num{
		width: 30px;
		text-align: center;
		font-size: 16px;
		font-weight: 600;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	#statcenter-errander-rank .list-item .rank-num img{
		width: 30px;
	}
	#statcenter-errander-rank .list-item .rider{
		width:85%;
	}
	#statcenter-errander-rank .list-item .rider .info{
		width: 60%;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
	}
	#statcenter-errander-rank .list-item .rider .avatar{
		width: 40px;
		height: 40px;
		border-radius: 50%;
		overflow: hidden;
		margin-right: 5px;
		border: 1px solid #f5f5f5;
	}
	#statcenter-errander-rank .list-item .rider .avatar img{
		width: 100%;
		height: 100%;
	}
	#statcenter-errander-rank .list-item .rider .name{
		width: 80%;
	}
	#statcenter-errander-rank .myself{
		padding: 0px 15px;
	}
	#statcenter-errander-rank .myself .list-item{
		border-radius: 200px;
		background-color: #f5f5f5;
		padding: 10px;
	}
</style>


